[data-btn], [data-btn]:link, [data-btn]:visited {
  background: var(--action);
  border-radius: var(--border);
  color: var(--background);
  display: inline-block;
  font-size: 1rem;
  font-style: normal;
  margin-bottom: var(--shim);
  padding: var(--border) var(--gutter);
  text-decoration: none;
  text-shadow: 0 -1px 0 var(--action-hover);
  transition: background-color 300ms, transform 350ms; }

[data-btn]:focus, [data-btn]:hover, [data-btn]:active {
  background: var(--action-hover);
  transform: scale(1.05); }

[data-field] {
  display: flex;
  flex-direction: column-reverse;
  margin-bottom: var(--gutter); }

[data-label] {
  background: var(--background);
  color: var(--field-chill);
  font-size: calc(var(--small) * 0.75);
  font-weight: bold;
  margin: 0 auto calc(0px - var(--shim)) var(--shim);
  padding: 0 var(--shim);
  transition: color 250ms ease; }

[data-input='default'],
[data-input='textarea'] {
  border: var(--border) solid var(--field-chill);
  border-radius: var(--shim);
  font-size: var(--small);
  padding: var(--border);
  transition: border-color 250ms ease;
  width: 100%; }
  [data-input='default']:invalid,
  [data-input='textarea']:invalid {
    border-color: var(--field-attention); }
  [data-input='default']:focus,
  [data-input='textarea']:focus {
    border-color: var(--field-chill);
    outline: 0; }
    [data-input='default']:focus + [data-label],
    [data-input='textarea']:focus + [data-label] {
      color: var(--field-attention); }

[data-input='textarea'] {
  min-height: calc(var(--rhythm) * 6); }

[data-actions] {
  align-items: center;
  display: flex;
  flex-direction: row-reverse; }

hr:not([data-layout='grid-border']) {
  font-family: "amp", "freight-text-pro", Baskerville, Palatino, Cambria, Georgia, serif;
  font-style: italic;
  color: var(--border);
  margin: var(--gutter) 0;
  padding: 0;
  text-align: center;
  width: 100%; }
  hr:not([data-layout='grid-border'])::after {
    content: "& & &"; }

.amp {
  font-family: "amp", "freight-text-pro", Baskerville, Palatino, Cambria, Georgia, serif;
  font-style: italic; }

blockquote,
.pullquote {
  font-size: var(--medium);
  font-style: italic;
  margin-bottom: var(--gutter); }
  blockquote em,
  blockquote i,
  .pullquote em,
  .pullquote i {
    font-style: normal; }
  blockquote .attribution,
  .pullquote .attribution {
    display: block;
    font-size: 0.8rem;
    font-style: normal;
    font-weight: bold;
    margin-top: calc(0px - var(--shim)); }

blockquote p:not(.attribution) {
  text-indent: -0.4em; }
  blockquote p:not(.attribution)::before, blockquote p:not(.attribution)::after {
    content: "";
    color: var(--accent-light);
    font-weight: bold; }
  blockquote p:not(.attribution)::after {
    content: ""; }

[data-layout='rst'] {
  margin-bottom: var(--spacer); }

.post-title {
  font-size: var(--h2);
  font-style: italic; }

.post-summary,
.event-summary {
  margin-bottom: var(--spacer); }

.byline,
.event-meta {
  font-size: var(--xsmall);
  font-variant: small-caps;
  grid-column: center / right; }
  .byline i,
  .event-meta i {
    font-variant: normal; }
  .byline a,
  .byline time,
  .byline b,
  .event-meta a,
  .event-meta time,
  .event-meta b {
    font-weight: bold; }
  @media (min-width: 42rem) {
    .byline,
    .event-meta {
      align-self: start;
      display: grid;
      grid-auto-flow: row;
      grid-auto-rows: auto;
      grid-column: left / center;
      text-align: right; } }
