// Fonts Config
// ============


// Body Font
// ---------
/// Freight Text Pro is the primary font of the OddBird brand.
/// @group fonts
/// @font body (regular, italic, bold, bold italic)
///   <script src="https://use.typekit.net/slx1xnq.js"></script>
///   <script>try{Typekit.load({ async: true });}catch(e){}</script>
$body-font: (
  'name': 'freight-text-pro',
  'stack': (Baskerville, Palatino, Cambria, Georgia, serif),
  'source': 'https://typekit.com/fonts/freight-text',
);

@include herman-font('body', $body-font);



// Code Font
// ---------
/// Source Code Pro provides a nice-looking monospace option
/// for code samples across the site.
/// @group fonts
/// @font code (regular, italic, bold, bold italic)
///   <script src="https://use.typekit.net/slx1xnq.js"></script>
///   <script>try{Typekit.load({ async: true });}catch(e){}</script>
$code-font: (
  'name': 'SourceCodePro',
  'stack': (Consolas, Menlo, Monaco, 'Courier New', monospace),
  'source': 'https://typekit.com/fonts/source-code-pro',
);

@include herman-font('code', $code-font);



// Ampersand Font
// --------------
/// Baskerville Italic provides a nice-looking ampersand.
/// @group fonts
$amp-font: (
  'name': 'amp',
  'stack': ('freight-text-pro', Baskerville, Palatino, Cambria, Georgia, serif),
  'italic': (
    'woff2': 'data:application/font-woff2;charset=utf-8;base64,',
    'woff': 'data:application/font-woff;charset=utf-8;base64,',
  ),
);

@include herman-font('amp', $amp-font);
